import Detail from "../../public/detail.vue"
import Person from "../../public/person.vue"
import Head from "../../public/head.vue"
import Bounced from '../../public/bounced.vue'
import Bus from '../../assets/eventBus'
import '../../../static/css/praise/iconfont.css'
import Loading from '../../public/Loading'
import loadingImg from '../../public/loadingImg'
  export default {
      name: "storyPage",
    components:{
        Head,
      Person,
      Detail,
      Bounced,
      Loading,
      loadingImg
    },
    data(){
        return{
          api: this.GLOBAL.baseURL,
          attation:0,
          user:'',
          imgList:[],
          create_time:'',
          value:5,
          imgUrl:'',
          firstoverflow:'hidden',
          overflow:'hidden',
          height:'80px',
          status:0,
          review:{
            text:'',
          },
          title:'',
          content:'',
          isLike:false,
          isLogin:true,
          isInform:false,
          changeReason:true,
          informReason:{
            text:''
          },
          isLove:false,
          changeTime:'',
          replay:'说点什么...',
          firstreplay:'说点什么...',
          firstheight:'80px',
          time:2,
          storyId:'',
          loveList:[],
          personList:[],
          storyList:[],
          wishList:[],
          newWish:false,
          wishNamea:'',
          wishIndex:0,
          saveIndex:-1,
          is_praise:'',
          informList:[],
          is_collection:'',
          jion:'1',
          total:0,
          isOwer:'',
          delet:true,
          isdelet:false,
          is_report:'',
          isLoading:false,
          replayChoose:'',
          isChat:false,
        }
    },
    watch:{
      wishNamea:function(){
        Bus.$emit('returnName',this.wishNamea)
      },
      storyId:function(){
        this.replayChoose = ''
        window.scroll(0,0)
        this.getComment(1)
        this.onLoading()
        this.getStory()
        this.lookStory()
        this.sendBuidu()
      }
    },
    methods:{
      sendBuidu(){
        this.$http.post(this.api + '/BaiduPush',{
          token: localStorage.getItem('token'),
          url: encodeURIComponent(window.location.href)
        })
          .then(res=>{
            if(res.data.code == 1){
              console.log('成功')
            }else if(res.data.code == 3){
              this.sendBuidu()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toPerson(item,index){
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.user.user_id
          }
        })
      },
      toAllLeave(a){
        localStorage.setItem('leave',JSON.stringify(a))
        this.$http.post(this.api + '/LeaveL',{
          token: localStorage.getItem('token'),
          flag: 5,
          order:1,
          page: 1,
          table_id: a.msg_id
        })
          .then(res=>{
            if(res.data.code == 1){
              a.leavemsg = res.data.data.data
              this.replayChoose = true
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.toAllLeave(a)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toStory(item,index){
        this.storyId = item.story_id
      },
      closeLove(){
        this.isLove = false
      },
      zan(){
        if(localStorage.getItem('isLogin')){
          let type= ''
          if(this.is_praise){
            type = 2
          }else{
            type = 1
          }
          this.$http.post(this.api + '/home/Comment/praise',{
            token: localStorage.getItem('token'),
            flag: 1,
            table_id: this.storyId,
            type: type
          })
            .then(res=>{
              if(res.data.code == 1){
                this.is_praise = type == 2? 0:1
                this.onLoading()
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.zan()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          this.$message({
            type: 'error',
            message: '您还没有登陆！'
          })
        }

        },
      closeWish(){
        this.isLike = false
      },
      openInform(item,index){
        if(localStorage.getItem('isLogin')){
          if(item != 1){
            if(item.is_report){
              alert('您已经举报了！')
            }else{
              localStorage.setItem('msg_id',item.msg_id)
              this.isInform = true
              this.getInform()
            }
          }else{
            if(this.is_report){
              alert('您已经举报了！')
            }else{
              this.isInform = true
              this.getInform()
            }
          }
        }else{
          this.$message({
            type: 'error',
            message: '您还没有登陆！'
          })
        }

      },
      //点赞
      addZan(item,index){
        if(item.zanIndex == 0){
          this.$http.post(this.api + '/home/Comment/praise',{
            token: localStorage.getItem('token'),
            flag: 1,
            table_id: item.story_id,
            type: 1
          })
            .then(res=>{
              if(res.data.code == 1){
                item.zanIndex = 1
                item.zan = item.zan + 1
              }else if(res.data.code == 3){
               this.$http.post(this.api + '/home/index/token')
                 .then(res=>{
                   localStorage.setItem('token',res.data.data)
                   this.addZan(item,index)
                 })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })

        }else{
          this.$http.post(this.api + '/home/Comment/praise',{
            token: localStorage.getItem('token'),
            flag: 1,
            table_id: item.story_id,
            type: 2
          })
            .then(res=>{
              if(res.data.code == 1){
                item.zan = item.zan - 1
                item.zanIndex = 0
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.addZan(item,index)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      firsttextareaFocus(){
        this.firstheight = 'auto'
        this.firstoverflow= 'none'
      },
      firstabolish(){
        this.review.text = ''
        this.firstheight = '80px'
        this.firstoverflow= 'hidden'
        this.firstreplay = '说点什么...'
      },
      abolish(){
        this.isChat = false
        this.review.text = ''
        this.height = '80px'
        this.overflow= 'hidden'
        this.replay = '说点什么...'
      },
      getLove(){
        this.isLove = true
        this.onLoading()
      },
      onLoading(){
        this.$http.post(this.api + '/storyp',{
          token: localStorage.getItem('token'),
          story_id: this.storyId
        })
          .then(res=>{
            if(res.data.code == 1){
              this.loveList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.onLoading()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      deleteLeave(a,b){
        this.$http.post(this.api + '/home/Comment/del_leavemsg',{
          token: localStorage.getItem('token'),
          msg_id: a.msg_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message:'删除评论成功！'
              })
              this.getComment(this.pager)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.deleteLeave(a,b)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      talk(a,b){
        if(a.user_id == this.isOwer){
          this.$confirm('此操作将删除您的留言, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteLeave(a,b)
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }else{
          this.isChat = true
          this.replay = '回复给' + a.user.name
          localStorage.setItem('msg_id',a.msg_id)
        }
      },
      firstdiscuss(){
        if(!this.review.text){
          this.$message({
            type: 'info',
            message: '请填写评论内容'
          })
        }else{
          this.$http.post(this.api + '/home/Comment/save_leavemsg',{
            token: localStorage.getItem('token'),
            flag: 2,
            table_id: this.storyId,
            content:  this.review.text
          })
            .then(res=>{
              if(res.data.code == 1){
                this.review.text = ''
                this.$message({
                  message:'评论成功',
                  type:'success'
                })
                this.getComment(1)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.firstdiscuss()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      discuss(){
        if(!this.review.text){
          this.$message({
            type: 'info',
            message: '请填写评论内容'
          })
        }else{
            this.$http.post(this.api + '/home/Comment/save_leavemsg',{
              token: localStorage.getItem('token'),
              flag: 5,
              content: this.review.text,
              table_id: localStorage.getItem('msg_id')
            })
              .then(res=>{
                if(res.data.code == 1){
                  localStorage.removeItem('msg_id')
                  this.isChat = false
                  this.review.text = ''
                  this.$message({
                    message:'评论成功',
                    type:'success'
                  })
                  this.getComment(this.pager)
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.discuss()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
        }
      },
      changeForm(){
        this.changeReason = false
      },
      informAblish(){
        this.changeReason = true
      },
      lookStory(){
        this.isLoading = true
        this.$http.post(this.api + '/home/Story/get_story',{
          token: localStorage.getItem('token'),
          story_id: this.storyId,
          visit: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              let b = [];
              for(let i =0;i<data.image.length;i++){
                b.push({img:data.image[i].domain + data.image[i].image_url})
              }
              this.imgList = b
              this.user = data.user
              this.is_praise = data.is_praise
              this.create_time = data.create_time
              this.content = data.content
              this.title = data.title
              this.is_collection = data.is_collection
              if(data.user.is_attention){
                this.attation = 1
              }else{
                this.attation = 0
              }
              this.is_report = data.is_report
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/hoem/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.lookStory()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      addAtention(){
        if(localStorage.getItem('isLogin')){
          let type = ''
          if(this.attation){
            type = 2
          }else{
            type = 1
          }
          this.$http.post(this.api + '/home/Comment/attention',{
            token: localStorage.getItem('token'),
            att_user_id: this.user.user_id,
            type: type,
          })
            .then(res=>{
              if(res.data.code == 1){
                this.attation = type == 2? 0:1
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.addAtention()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          alert('您还没有登陆')
        }
      },
      like(){
        if(localStorage.getItem('isLogin')){
          this.isLike = !this.isLike
          this.getRevie()
        }else{
          this.$message({
            type: 'error',
            message: '您还没有登陆！'
          })
        }
      },
      getRevie(){
        this.$http.post(this.api + '/home/Comment/collegroup_list',{
          token: localStorage.getItem('token'),
          table_id: this.storyId,
          flag: 2
        })
          .then(res=>{
            if(res.data.code == 1){
              this.wishList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getRevie()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })

      },
      addwish(){
        if(this.wishIndex == 0){
          this.newWish = true
          this.wishIndex = -1
        }else{
          this.newWish = false
          this.wishIndex = 0
        }
      },
      addWishName(){
        if(this.wishNamea == ' '||this.wishNamea.split(' ').join("").length == 0){
          this.$message({
            type:'error',
            message: '保存心愿单的过程中遇到了问题，请重试。'
          })
        }else{
          this.$http.post(this.api + '/home/Comment/add_collegroup',{
            token: localStorage.getItem('token'),
            group_name: this.wishNamea
          })
            .then(res=>{
              if(res.data.code == 1){
                this.wishNamea = ''
                this.getRevie();
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.addWishName()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })

        }
      },
      save(item,index){
        if(item.is_this_colle == 1){
          this.$http.post(this.api + '/home/Comment/collection',{
            token: localStorage.getItem('token'),
            flag: 2,
            table_id: this.storyId,
            group_id: item.group_id,
            type: 2
          })
            .then(res=>{
              if(res.data.code == 1){
                let a=[]
                item.is_this_colle = 0
                for(let i =0;i<this.wishList.length;i++){
                  if(this.wishList[i].is_this_colle == 0){
                    a.push(i)
                    if(a.length == this.wishList.length){
                      this.is_collection = 0
                    }
                  }
                }
              }else if(res.data.code == 1){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.save(item,index)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          this.$http.post(this.api + '/home/Comment/collection',{
            token: localStorage.getItem('token'),
            flag: 2,
            table_id: this.storyId,
            group_id: item.group_id,
            type: 1
          })
            .then(res=>{
              if(res.data.code == 1){
                item.is_this_colle = 1
                this.is_collection = 1
              }else if(res.data.code == 1){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      getName(data){
        this.wishNamea = data
      },
      getInform(){
        this.$http.post(this.api + '/home/Activity/question',{
          token: localStorage.getItem('token'),
          flag: 6
        })
          .then(res=>{
            if(res.data.code == 1){
              this.informList = []
              for(let i = 0;i<res.data.data[0].option.length;i++){
                let a = res.data.data[0].option[i].name.split('/')
                this.informList.push({name: a[0],content:a[1],option_id:res.data.data[0].option[i].option_id})
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getInform()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      closeInform(item){
        let a = localStorage.getItem('msg_id')
        if(a){
          if(item){
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: 4,
              table_id: a,
              option_id: item.option_id
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  this.$message({
                    type:'success',
                    message:'提交成功'
                  })
                  this.getComment(this.pager)
                  localStorage.removeItem('msg_id')
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.closeInform(item)
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }else{
            this.isInform = false
            localStorage.removeItem('msg_id')
          }
        }else{
          if(item){
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: 3,
              table_id: this.storyId,
              option_id: item.option_id
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  this.$message({
                    type:'success',
                    message:'提交成功'
                  })
                  this.is_report = '1'
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.closeInform(item)
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }else{
            this.isInform = false
          }
        }
      },
      informSend(){
        let a = localStorage.getItem('msg_id')
        if(a){
          if(this.informReason.text.trim().length <= 0){
            this.$message({
              type:'error',
              message: '请填写您的理由'
            })
          }else{
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: 4,
              table_id: a,
              content: this.informReason.text
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  this.informReason.text = ''
                  this.changeReason = true
                  this.$message({
                    type:'success',
                    message:'提交成功'
                  })
                  localStorage.removeItem('msg_id')
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.informSend()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        }else{
          if(this.informReason.text.trim().length <= 0){
            this.$message({
              type:'error',
              message: '请填写您的理由'
            })
          }else{
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: 3,
              table_id: this.storyId,
              content: this.informReason.text
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  this.informReason.text = ''
                  this.changeReason = true
                  this.$message({
                    type:'success',
                    message:'提交成功'
                  })
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.informSend()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        }

      },
      takeAttention(item,index){
        if(localStorage.getItem('isLogin')){
          let type = ''
          if(item.is_attention){
            type = 2
          }else{
            type = 1
          }
          this.$http.post(this.api + '/home/Comment/attention',{
            token: localStorage.getItem('token'),
            att_user_id: item.user_id,
            type: type,
          })
            .then(res=>{
              if(res.data.code == 1){
                item.is_attention = type == 2? 0:1
                if(this.user.user_id == item.user_id){
                  this.attation = type == 2? 0:1
                }
              }else if(res.data.code == 3){
               this.$http.post(this.api + '/home/index/token')
                 .then(res=>{
                   localStorage.setItem('token',res.data.data)
                   this.takeAttention(item,index)
                 })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      //获取评论
      getComment(val){
        this.$http.post(this.api + '/LeaveL',{
          token: localStorage.getItem('token'),
          table_id:this.storyId,
          flag: 2,
          order: 1,
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data.data
              this.personList = data
              this.total = res.data.data.total
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getComment(val)
                })
            }else if(res.data.cdoe == 0){
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
        this.pager = val
        this.getComment(val)
      },
      //相似故事
      getStory(){
        this.$http.post(this.api + '/storys',{
          token: localStorage.getItem('token'),
          story_id: this.storyId
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              var m = []
              for(let i = 0;i<data.length;i++){
                let user = data[i].user
                  m.push({
                      img:data[i].cover_image?data[i].cover.domain + data[i].cover.image_url:'../../static/img/static/back.jpg',
                      address:data[i].address,
                      remark: data[i].title,
                      avator:user.head_image?user.headimage.domain + user.headimage.image_url: '../../static/img/static/user.png',
                      zan: data[i].praise_num,
                      say: data[i].leaving_num,
                      zanIndex:data[i].is_praise?1:0,
                      story_id: data[i].story_id,
                      userId: data[i].user_id,
                      leaving_num: data[i].leaving_num
                    })
              }
              this.storyList = m
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getStory()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })

      },
      //评论点赞
      parise(item,index){
        let type=''
        if(item.is_praise){
          type = 2
        }else{
          type = 1
        }
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 4,
          table_id: item.msg_id,
          type: type
        })
          .then(res=>{
            if(res.data.code == 1){
              item.is_praise = type == 2? 0 : 1
              item.praise_num = type == 2? item.praise_num - 1 : item.praise_num +1
            }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
               .then(res=>{
                 localStorage.setItem('token',res.data.data)
                 this.parise(item,index)
               })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //查询用户
      getOwer(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data[0]
              this.imgUrl = data.head_image? data.headimage.domain + data.headimage.image_url : '../../../static/img/static/user.png'
              this.isOwer = data.user_id
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOwer()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toperson(item,index){
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.userId
          }
        })
      },
      ALltoperson(){
        this.$router.push({
          path: '/userdata',
          query: {
            information: this.user.user_id
          }
        })
      }
    },
    mounted(){
        window.scroll(0,0)
        let _this = this
        if(localStorage.getItem('isLogin')){
          _this.isLogin = true
          _this.getOwer()
        }else{
          _this.isLogin = false
        }
    },
    created() {

      this.storyId = this.$route.query.information
      window.scroll(0,0)
    }
  }



